.navigation {
  @include verticalAlign;
  flex-direction: column;
  padding: 20px;
  ul {
    @include verticalAlign;
    margin-top: 20px;

    li {
      padding: 10px;
      transform: scale(1);
      &:hover {
        transform: scale(1.1);
      }
      a {
        @include btnStyle;
        padding: 15px;
        &:hover {
          @include btnStyleHover;
        }
      }
    }
  }
}
