.formMovie {
  @include verticalAlign;
  flex-direction: column;
  form {
    @include verticalAlign;
    flex-direction: column;
    input {
      width: 100%;
      padding: 10px;
      margin: 10px 0;
      border: 1px solid $colorBorder;
      border-radius: 10px;
      &:focus {
        outline: none;
      }
    }
    input[type="submit"] {
      @include btnStyle;
      padding: 10px;
      &:hover {
        @include btnStyleHover;
      }
    }
  }

  .topflop {
    @include btnStyle;
    padding: 5px;
    margin: 10px 10px;
    &:hover {
      @include btnStyleHover;
    }
  }
}
