.card {
  border: $colorBorder 10px solid;
  margin: 10px;
  background: $colorBg;
  align-items: center;
  justify-content: center;
  width: 400px;
  height: 600px;
  text-align: center;

  .headerCard {
    position: relative;

    img {
      width: 100%;
    }

    .inactif {
      opacity: 0;
    }

    .actif {
      opacity: 1;
    }
    .likeContainer {
      position: absolute;
      top: 10px;
      left: 10px;
      .likeIconWrapper {
        position: relative;
        width: 2rem;
        height: 2rem;
        top: 0;

        .actif,
        .inactif {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
        &:hover {
          transform: scale(1.2);
          cursor: pointer;
        }

        &:hover .inactif {
          opacity: 1;
        }

        &:hover .actif {
          opacity: 0;
        }
      }
    }
  }
  .synopsis {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    overflow: hidden;
  }
}
