.home {
  .movies {
    margin-top: 10px;
    display: grid;
    grid-template-columns: repeat(auto-fill, 400px);
    gap: 20px;
    justify-content: center;
    position: relative;
    .loadingContainer {
      position: absolute;
      top: 20vh;
      left: 50vw;
      z-index: 99;
      @include verticalAlign;
      flex-direction: column;
    }
  }
}
