$colorBorder: #2b86c5;
// #7f5890;
$colorBtn: #6c53ef;
$colorBg: #030303;
$colorTxt: #fdfdfd;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #000000;
  background-image: linear-gradient(
    225deg,
    #ff3cac 0%,
    #784ba0 50%,
    #2b86c5 100%
  );
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;

  // min-height: 100vh;
  color: $colorTxt;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
@mixin verticalAlign {
  display: flex;
  align-items: center;
  justify-content: center;
}
@mixin btnStyleHover {
  cursor: pointer;
  background: $colorBg;
  transform: scale(1.1);
}

@mixin btnStyle {
  transform: scale(1);
  text-decoration: none;
  background: $colorBtn;
  color: $colorTxt;
  padding: 20px;
  border-radius: 10px;
}

li {
  list-style: none;
}
